import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { Welcome } from '../pages/welcome/welcome';
import { Login } from '../pages/login/login';
import { Signup } from '../pages/signup/signup';
import { AboutPage } from '../pages/about/about';
import { ContactPage } from '../pages/contact/contact';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import { SettingPage } from '../pages/setting/setting';
import { SettingPageModule } from '../pages/setting/setting.module';
import { ReimbursementPage } from '../pages/reimbursement/reimbursement';
import { ReimbursementPageModule } from '../pages/reimbursement/reimbursement.module';
import { ApplyreimbursementPage } from '../pages/applyreimbursement/applyreimbursement';

//借款
import { BorrowManagePage } from '../pages/borrowmanage/borrowmanage';
import { BorrowManagePageModule } from '../pages/borrowmanage/borrowmanage.module';
import { ApplyborrowmoneyPage } from '../pages/applyborrowmoney/applyborrowmoney';
import { ApplyborrowmoneyPageModule } from '../pages/applyborrowmoney/applyborrowmoney.module';
import { BorrowDetailPageModule } from '../pages/borrow-detail/borrow-detail.module';
import { BorrowReturnPageModule } from '../pages/borrow-return/borrow-return.module';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { MultiPickerModule } from 'ion-multi-picker';

@NgModule({
  declarations: [
    MyApp,
    Welcome,
    Login,
    Signup,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,
    // ReimbursementPage,
    ApplyreimbursementPage,
    // SettingPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp,{
      backButtonText:'',
      cancelButton:' 取消'
    }, 
{
  links: [
    { loadChildren: '../pages/login/login.module#LoginModule', name: 'Login', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signup/signup.module#SignupModule', name: 'Signup', segment: 'signup', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/welcome/welcome.module#WelcomeModule', name: 'Welcome', segment: 'welcome', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/setting/setting.module#SettingPageModule', name: 'SettingPage', segment: 'setting', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/applyreimbursement/applyreimbursement.module#ApplyreimbursementPageModule', name: 'ApplyreimbursementPage', segment: 'applyreimbursement', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/reimbursement/reimbursement.module#ReimbursementPageModule', name: 'ReimbursementPage', segment: 'reimbursement', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/borrowmanage/borrowmanage.module#BorrowManagePageModule', name: 'BorrowManagePage', segment: 'borrowmanage', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/borrow-return/borrow-return.module#BorrowReturnPageModule', name: 'BorrowReturnPage', segment: 'borrow-return', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/borrow-send/borrow-send.module#BorrowSendPageModule', name: 'BorrowSendPage', segment: 'borrow-send', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/applyborrowmoney/applyborrowmoney.module#ApplyborrowmoneyPageModule', name: 'ApplyborrowmoneyPage', segment: 'applyborrowmoney', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/borrow-detail/borrow-detail.module#BorrowDetailPageModule', name: 'BorrowDetailPage', segment: 'borrow-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/number/number.module#NumberPageModule', name: 'NumberPage', segment: 'number', priority: 'low', defaultHistory: [] }
  ]
}),
    SettingPageModule,
    ReimbursementPageModule,
    BorrowManagePageModule,
    BorrowDetailPageModule,
    ApplyborrowmoneyPageModule,
    // BorrowReturnPageModule,
    MultiPickerModule 
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    Welcome,
    Login,
    Signup,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,
    SettingPage,
    ReimbursementPage,
    ApplyreimbursementPage,
    BorrowManagePage,
    ApplyborrowmoneyPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



